import { _decorator, Component, Node, Prefab, instantiate } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Energy')
export class Energy extends Component {
    @property({ type: Prefab })
    energyPre: Prefab | null = null;

    energyX = -125;

    start() {
        let node = new Node("box")
        node.setParent(this.node.parent);
    }

    update(deltaTime: number) {
        if (this.energyX > 100) {
            this.energyX = -125;
            this.node.parent.getChildByName("box").destroy();
            let node = new Node("box")
            node.setParent(this.node.parent);
        }
    }

    energy() {
        let energy = instantiate(this.energyPre);
        energy.setParent(this.node.parent.getChildByName("box"));
        let node = this.node.position;
        energy.setPosition(this.energyX, node.y, 0);
        this.energyX += 25;
    }
}

